@keyframes circle-spinner{
    0%{
        transform: rotate(0deg);
    }
    100%{
        transform: rotate(360deg);
    }
}
.spinner.circle{
    border: 2px solid #27ae60;
    border-right: 2px solid #eee;
    width:100px;
    height:100px;
    border-radius: 50%;
    animation: circle-spinner 0.5s infinite ease-out;
}